.job-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.job-card {
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  padding: 1.5rem;
  transition: all 0.3s ease;
  border: 1px solid transparent;
}

.job-card:hover {
  transform: translateY(-6px);
  border-color: #007bff;
  box-shadow: 0 6px 20px rgba(0, 123, 255, 0.15);
}

.title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #222;
}

.company {
  font-weight: 500;
  color: #555;
  margin-bottom: 0.25rem;
}

.location, .salary {
  font-size: 0.95rem;
  color: #666;
  margin-bottom: 0.25rem;
}

.description {
  font-size: 0.9rem;
  color: #444;
  margin: 0.5rem 0 1rem 0;
  line-height: 1.4;
}

.apply-btn {
  background: linear-gradient(90deg, #007bff, #00c6ff);
  border: none;
  color: #fff;
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: 0.3s ease;
}

.apply-btn:hover {
  background: linear-gradient(90deg, #0056b3, #00aaff);
}
